@import '../../index.css';

.grid {
	width: 100%;
	margin-top: 100px;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-flow: row wrap;
}

.grid-item {
	flex: 1 0 calc(100% / 3); 
	height: calc(100vw / 3);
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.grid-link {
	text-decoration: none;
	display: flex;
	flex: 1 0 calc(100% / 3); 
	height: calc(100vw / 3);
}

.grid-img {
	width: 60%;
	height: 200px;
	max-width: 200px;
	max-height: 200px;
	min-width: 100px;
	min-height: 100px;

	margin-right: 10px;
	margin-left: 10px;
	margin-top: 10px;
}

.grid-text {
	visibility: hidden;
	margin-top: 20px;
	font-weight: 700;
	font-size: 16px;
	z-index: 1;
}

.grid-item:hover {
	opacity: 0.7;
	cursor: pointer;
}

.grid-item:hover .grid-text {
	visibility: visible;
	display: block;
	color: white;
}

.skyscraper {
	background-color: var(--skyscraper);
}

.sudoku {
	background-color: var(--sudoku);
}

.stela {
	background-color: var(--stela);
}

.p2d {
	background-color: var(--p2d);
}

.qstyle {
	background-color: var(--qstyle);
}

.teapot {
	background-color: var(--teapot);
}

.thesis {
	background-color: var(--thesis);
}

.rietvelt {
	background-color: var(--rietvelt);
}

@media only screen and (max-width: 700px) {
	.grid {
		flex-direction: column;
	}

	.grid-item {
		flex: 0 1 auto;
		width: 100vw;
		height: 100vw;
	}

	.grid-link {
		flex: 0 1 auto;
		width: 100vw;
		height: 100vw;
	}

	.grid-item:active .grid-text {
		visibility: visible;
		display: block;
		color: white;
	}

	.p2d:active {
		background-color: rgba(150, 221, 217, 0.7);
	}

	.skyscraper:active {
		background-color: rgba(55, 139, 247, 0.7);
	}

	.sudoku:active {
		background-color: rgba(251, 153, 56, 0.7);
	}

	.stela:active {
		background-color: rgba(16, 69, 113, 0.7);
	}
} 