@import '../../index.css';

.titlebar-container {
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
}

.titlebar {
	width: 100%;
	display: flex;
	flex-grow: 1;
	padding-top: 20px;
	padding-bottom: 20px;
	align-items: center;
	position: fixed;
	background-color: white;
	z-index: 10;
	top: 0;
}

.titlebar-left {
	display: flex;
	width: calc(100% / 3);
}

.nav-item {
	margin-left: 15px;
	cursor: pointer;
	color: #aaa;
	font-size: 18px;
	font-weight: 400px;
	display: flex;
	align-items: center;
}

.nav-item:hover {
	opacity: 0.6;
}

.nav-button {
	margin-left: 3px;
	margin-right: 3px;
}

.nav-button:hover {
	filter: brightness(50%);
}

.titlebar-center {
	width: calc(100% / 3);
	height: 100%;
	color: black !important;
}

.titlebar-right {
	width: calc(100% / 3);
	display: flex;
	align-items: center;
	justify-content: flex-end;
}

.title {
	height: 100%;
	width: 100%;
	font-size: 36px;
	margin-bottom: 0;
}

.byline {
	font-size: 16px;
	font-weight: 300px;
	margin-top: 0;
}

.hamburger {
	display: none;
	height: 100%;
	max-width: 45px;
	cursor: pointer;
}

.hamburger:hover {
	opacity: 0.7;
}

.hamburger-menu {
	top: 0;
	position: fixed;
	height: 50px;
	width: 100%;
	background-color: #ccc;
	align-items: center;
	z-index: 10;
	justify-content: flex-end;
	pointer-events: initial !important;

	-webkit-transition: opacity 10s ease;
  -moz-transition: ease-in 2s none;
  -ms-transition: ease-in  2s none;
  -o-transition: ease-in  2s none;
  transition: ease-in  2s none; 
}

.hamburger-menu__text {
	padding-top: 5px;
	padding-bottom: 5px;
	width: 100%;
	color: white;
	font-weight: 600;
}

.hamburger-menu__text:active, .hamburger-menu__text:hover {
	opacity: 0.7;
}

.contact-buttons {
	margin-right: 5vw;
	display: flex;
	justify-content: center;
	margin-left: 15px;
}

@media only screen and (min-width: 710px) {
	.hamburger-menu {
		display: none;
	}
}

@media only screen and (max-width: 700px) {
	.titlebar-left, .nav-item {
		display: none;
	}

	.titlebar {
		justify-content: center;
	}

	.titlebar-center {
		width: auto;
		visibility: visible !important;
		color: white;
	}

	.titlebar-right {
		justify-content: center;
		width: auto;
	}

	.hamburger {
		display: block;
	}

	.byline {
		display: none;
	}

	.hamburger-menu {
		display: block;
	}

	.contact-buttons {
		width: 100%;
		height: 100%;
		align-items: center;
	}
}

.link {
	text-decoration: none;
	color: inherit;
}
