.full-image {
	height: 100%;
	width: 100%;
	position: absolute;
}

.full-img {
	width: 100%;
}
.page-container {
	display: flex;
	align-items: flex-start;
	flex-direction: column;
	pointer-events: none;
}
.portfolio-container {
	display: flex;
	height: calc(100vh - 50px);
	align-items: center;
	justify-content: center;
	flex-direction: column;
	z-index: 100;
}

.portfolio-img:hover {
	filter: brightness(80%);
	cursor: pointer;
}

.titlebar-container {
	width: 100%;
	display: flex;
	align-items: center;
  justify-content: center;
  text-align: center;
  z-index: 10;
}

.image-container {
	display: flex;
	flex-direction: column;
	float: left;
	margin-top: 100px;
}

.portfolio-img {
	height: calc(100vh - 150px);
	background-color: black;
	max-height: 400px;
}

.text-container {
	width: 100%;
	height: 100%;
	position: absolute;
	display: flex;
	align-items: center;
	justify-content: center;
	font-size: 20px;
}

.text-full {
	width: 50vw;
}

.dot-container {
	display: flex;
	align-items: center;
	justify-content: center;
	position: fixed;
}

.page-title {
	margin-left: 5vw;
	font-size: 18px;
	display: flex;
	align-items: center;
	position: fixed;
	left: 0;
}

.page-link {
	margin-right: 5vw;
	font-size: 18px;
	display: flex;
	align-items: center;
	position: absolute;
	right: 0;
	pointer-events: initial !important;
	z-index: 100;
}

.dot {
  height: 9px;
  width: 9px;
  border-radius: 50%;
  display: inline-block;
  margin-left: 2px;
  margin-right: 2px;
}

.dot:hover {
	background-color: #333 !important;
	cursor: pointer;
}

.selected {
	background-color: #333 !important;
}

.page-footer {
	width: 100%;
	height: 50px;
	display: flex;
	align-items: center;
	justify-content: center;
}

.split-screen {
	position: absolute;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
	display: flex;
}

.split-left {
	width: 50%;
	cursor: w-resize;
}

.split-right {
	width: 50%;
	cursor: e-resize;
}

@media only screen and (max-width: 700px) {
	.portfolio-img {
		max-height: 40vh;
     	width: auto;
	}

	.portfolio-container {
		position: static;
		flex-direction: column;
		width: 100vw;
		height: 78vh;
		display: flex;
		align-items: center;
		vertical-align: top;
	}

	.text-container {
	width: 40vw;
	font-size: 20px;
	display: flex;
	align-items: center;
	justify-content: center;
	text-align: left;
	z-index: 1;
	pointer-events: none;
}

	.image-container {
		margin-top: 0;
	}

	.text-container {
		width: 100%;
		margin-left: 0;
		position: absolute;
		font-size: 16px;
	}

	.page-footer {
		width: 100%;
		justify-content: center;
	}

	.page-title {
		width: calc(100% / 3);
	}

	.portfolio-img:hover {
		filter: brightness(100%);
		cursor: pointer;
	}

	.page-title, .page-link {
		font-size: 14px;
	}

	.full-img {
		height: 100vh;
		width: auto;
		margin-top: 100px;
	}

	.full-image {
		display: flex;
		justify-content: center;
	}
}