@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,600,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap);
:root {
  --skyscraper: #3189E6;
  --sudoku: #FD9A27; 
  --stela: #0C4473;
  --p2d: #94DDD9;
  --qstyle: rgb(181, 116, 150);
  --teapot: #de1d64;
  --thesis: #000;
  --rietvelt: rgb(194, 192, 181);
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
   font-family: 'Open Sans', sans-serif;
   font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.titlebar-container {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.grid-container {
  width: 100%;
}
.titlebar-container {
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
}

.titlebar {
	width: 100%;
	display: flex;
	flex-grow: 1;
	padding-top: 20px;
	padding-bottom: 20px;
	align-items: center;
	position: fixed;
	background-color: white;
	z-index: 10;
	top: 0;
}

.titlebar-left {
	display: flex;
	width: calc(100% / 3);
}

.nav-item {
	margin-left: 15px;
	cursor: pointer;
	color: #aaa;
	font-size: 18px;
	font-weight: 400px;
	display: flex;
	align-items: center;
}

.nav-item:hover {
	opacity: 0.6;
}

.nav-button {
	margin-left: 3px;
	margin-right: 3px;
}

.nav-button:hover {
	-webkit-filter: brightness(50%);
	        filter: brightness(50%);
}

.titlebar-center {
	width: calc(100% / 3);
	height: 100%;
	color: black !important;
}

.titlebar-right {
	width: calc(100% / 3);
	display: flex;
	align-items: center;
	justify-content: flex-end;
}

.title {
	height: 100%;
	width: 100%;
	font-size: 36px;
	margin-bottom: 0;
}

.byline {
	font-size: 16px;
	font-weight: 300px;
	margin-top: 0;
}

.hamburger {
	display: none;
	height: 100%;
	max-width: 45px;
	cursor: pointer;
}

.hamburger:hover {
	opacity: 0.7;
}

.hamburger-menu {
	top: 0;
	position: fixed;
	height: 50px;
	width: 100%;
	background-color: #ccc;
	align-items: center;
	z-index: 10;
	justify-content: flex-end;
	pointer-events: initial !important;

	-webkit-transition: opacity 10s ease;
  -webkit-transition: ease-in  2s none;
  transition: ease-in  2s none; 
}

.hamburger-menu__text {
	padding-top: 5px;
	padding-bottom: 5px;
	width: 100%;
	color: white;
	font-weight: 600;
}

.hamburger-menu__text:active, .hamburger-menu__text:hover {
	opacity: 0.7;
}

.contact-buttons {
	margin-right: 5vw;
	display: flex;
	justify-content: center;
	margin-left: 15px;
}

@media only screen and (min-width: 710px) {
	.hamburger-menu {
		display: none;
	}
}

@media only screen and (max-width: 700px) {
	.titlebar-left, .nav-item {
		display: none;
	}

	.titlebar {
		justify-content: center;
	}

	.titlebar-center {
		width: auto;
		visibility: visible !important;
		color: white;
	}

	.titlebar-right {
		justify-content: center;
		width: auto;
	}

	.hamburger {
		display: block;
	}

	.byline {
		display: none;
	}

	.hamburger-menu {
		display: block;
	}

	.contact-buttons {
		width: 100%;
		height: 100%;
		align-items: center;
	}
}

.link {
	text-decoration: none;
	color: inherit;
}

.grid {
	width: 100%;
	margin-top: 100px;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-flow: row wrap;
}

.grid-item {
	flex-grow: 1;
	flex-shrink: 0;
	flex-basis: calc(100% / 3); 
	height: calc(100vw / 3);
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.grid-link {
	text-decoration: none;
	display: flex;
	flex-grow: 1;
	flex-shrink: 0;
	flex-basis: calc(100% / 3); 
	height: calc(100vw / 3);
}

.grid-img {
	width: 60%;
	height: 200px;
	max-width: 200px;
	max-height: 200px;
	min-width: 100px;
	min-height: 100px;

	margin-right: 10px;
	margin-left: 10px;
	margin-top: 10px;
}

.grid-text {
	visibility: hidden;
	margin-top: 20px;
	font-weight: 700;
	font-size: 16px;
	z-index: 1;
}

.grid-item:hover {
	opacity: 0.7;
	cursor: pointer;
}

.grid-item:hover .grid-text {
	visibility: visible;
	display: block;
	color: white;
}

.skyscraper {
	background-color: var(--skyscraper);
}

.sudoku {
	background-color: var(--sudoku);
}

.stela {
	background-color: var(--stela);
}

.p2d {
	background-color: var(--p2d);
}

.qstyle {
	background-color: var(--qstyle);
}

.teapot {
	background-color: var(--teapot);
}

.thesis {
	background-color: var(--thesis);
}

.rietvelt {
	background-color: var(--rietvelt);
}

@media only screen and (max-width: 700px) {
	.grid {
		flex-direction: column;
	}

	.grid-item {
		flex: 0 1 auto;
		width: 100vw;
		height: 100vw;
	}

	.grid-link {
		flex: 0 1 auto;
		width: 100vw;
		height: 100vw;
	}

	.grid-item:active .grid-text {
		visibility: visible;
		display: block;
		color: white;
	}

	.p2d:active {
		background-color: rgba(150, 221, 217, 0.7);
	}

	.skyscraper:active {
		background-color: rgba(55, 139, 247, 0.7);
	}

	.sudoku:active {
		background-color: rgba(251, 153, 56, 0.7);
	}

	.stela:active {
		background-color: rgba(16, 69, 113, 0.7);
	}
} 
.full-image {
	height: 100%;
	width: 100%;
	position: absolute;
}

.full-img {
	width: 100%;
}
.page-container {
	display: flex;
	align-items: flex-start;
	flex-direction: column;
	pointer-events: none;
}
.portfolio-container {
	display: flex;
	height: calc(100vh - 50px);
	align-items: center;
	justify-content: center;
	flex-direction: column;
	z-index: 100;
}

.portfolio-img:hover {
	-webkit-filter: brightness(80%);
	        filter: brightness(80%);
	cursor: pointer;
}

.titlebar-container {
	width: 100%;
	display: flex;
	align-items: center;
  justify-content: center;
  text-align: center;
  z-index: 10;
}

.image-container {
	display: flex;
	flex-direction: column;
	float: left;
	margin-top: 100px;
}

.portfolio-img {
	height: calc(100vh - 150px);
	background-color: black;
	max-height: 400px;
}

.text-container {
	width: 100%;
	height: 100%;
	position: absolute;
	display: flex;
	align-items: center;
	justify-content: center;
	font-size: 20px;
}

.text-full {
	width: 50vw;
}

.dot-container {
	display: flex;
	align-items: center;
	justify-content: center;
	position: fixed;
}

.page-title {
	margin-left: 5vw;
	font-size: 18px;
	display: flex;
	align-items: center;
	position: fixed;
	left: 0;
}

.page-link {
	margin-right: 5vw;
	font-size: 18px;
	display: flex;
	align-items: center;
	position: absolute;
	right: 0;
	pointer-events: initial !important;
	z-index: 100;
}

.dot {
  height: 9px;
  width: 9px;
  border-radius: 50%;
  display: inline-block;
  margin-left: 2px;
  margin-right: 2px;
}

.dot:hover {
	background-color: #333 !important;
	cursor: pointer;
}

.selected {
	background-color: #333 !important;
}

.page-footer {
	width: 100%;
	height: 50px;
	display: flex;
	align-items: center;
	justify-content: center;
}

.split-screen {
	position: absolute;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
	display: flex;
}

.split-left {
	width: 50%;
	cursor: w-resize;
}

.split-right {
	width: 50%;
	cursor: e-resize;
}

@media only screen and (max-width: 700px) {
	.portfolio-img {
		max-height: 40vh;
     	width: auto;
	}

	.portfolio-container {
		position: static;
		flex-direction: column;
		width: 100vw;
		height: 78vh;
		display: flex;
		align-items: center;
		vertical-align: top;
	}

	.text-container {
	width: 40vw;
	font-size: 20px;
	display: flex;
	align-items: center;
	justify-content: center;
	text-align: left;
	z-index: 1;
	pointer-events: none;
}

	.image-container {
		margin-top: 0;
	}

	.text-container {
		width: 100%;
		margin-left: 0;
		position: absolute;
		font-size: 16px;
	}

	.page-footer {
		width: 100%;
		justify-content: center;
	}

	.page-title {
		width: calc(100% / 3);
	}

	.portfolio-img:hover {
		-webkit-filter: brightness(100%);
		        filter: brightness(100%);
		cursor: pointer;
	}

	.page-title, .page-link {
		font-size: 14px;
	}

	.full-img {
		height: 100vh;
		width: auto;
		margin-top: 100px;
	}

	.full-image {
		display: flex;
		justify-content: center;
	}
}
